html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

::-moz-selection {
  color: whitesmoke;
  background: #39ba69;
}

::selection {
  color: whitesmoke;
  background: #39ba69;
}

::-moz-selection {
color: whitesmoke;
background: #39ba69;
}

*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

h3 {
  font-weight: bold;
}

.top-container {
  background: -webkit-gradient(linear,right top, left top,from(#4d628e),to(#32405b));
  background: -webkit-linear-gradient(right,#4d628e,#32405b);
  background: -o-linear-gradient(right,#4d628e,#32405b);
  background: linear-gradient(-90deg,#4d628e,#32405b);
	position: relative;
}

.middle-container {
  transform:skew(0deg,-5deg);
	-ms-transform:skew(0deg,-10deg);
	-webkit-transform:skew(0deg,-4deg);
  margin-top:-50px;
  background: whitesmoke;
}

.middle-container-wrapper {
  transform:skew(0deg,5deg);
	-ms-transform:skew(0deg,10deg);
	-webkit-transform:skew(0deg,4deg);
}

.my-button {
	height: 40px;
	border-radius: 4px;
	font-size: 20px;
	font-family: Nunito;
	font-weight: 700;
	color: #2b374f;
	background: #0ba343;
	border: none;
	-webkit-box-shadow: 2px 6px 30px rgba(0, 0, 0, 0.2);
	        box-shadow: 2px 6px 30px rgba(0, 0, 0, 0.2);
	outline: none !important;
	padding: 0px 12px;
}

.my-button:hover {
	cursor: pointer;
	-webkit-box-shadow: 2px 6px 30px rgba(0, 0, 0, 0.3);
	        box-shadow: 2px 6px 30px rgba(0, 0, 0, 0.3);
	background: #28a857;
}

.transition-blue {
  background: blue;
  height: 200px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

@media (min-width: 1441px) {
  .middle-container {
    transform:skew(0deg,-3deg);
  	-ms-transform:skew(0deg,-7deg);
  	-webkit-transform:skew(0deg,-2deg);
    margin-top:-50px;
    background: whitesmoke;
  }
  .middle-container-wrapper {
    transform:skew(0deg,3deg);
  	-ms-transform:skew(0deg,7deg);
  	-webkit-transform:skew(0deg,2deg);
  }
}



