.contact-container {
	background: -webkit-gradient(linear,left bottom, left top,from(#cbd0d6),to(#eaecef));
	background: -webkit-linear-gradient(bottom,#cbd0d6,#eaecef);
	background: -o-linear-gradient(bottom,#cbd0d6,#eaecef);
	background: linear-gradient(0deg,#cbd0d6,#eaecef);
	height: 860px;
	margin-top:-50px;
  -ms-flex-pack: center;
      justify-content: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}

.contact-form {
	margin-top: 120px;
	color: #2b374f;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
}

.contact-form input, textarea {
	display: block;
	font-family: Open Sans !important;
	background: none;
	border: none;
	margin-bottom: 50px;
	outline: none;
	border-bottom: solid 2px #b8bfc6;
	height: 30px;
	width: 200px;
	font-size: 16px;
	padding-left: 5px;
}

.contact-form textarea {
	height: 150px;
	background: none;
	border: solid 2px #b8bfc6;
	width: 400px;
}

.contact-message {
	font-family: Nunito;
	font-size: 40px;
	margin-bottom: 50px;
	color: #2b374f;
}

.my-footer {
	font-family: Nunito;
	color: #767c87;
	z-index: 500;
	width: 100%;
	text-align: center;
	margin-top: 180px;
}

.my-footer a {
	color: #767c87;
	font-weight: bold;
}


@media (max-width: 425px) {
	.contact-form textarea {
		width: 250px;
		height: 200px;
	}

	.contact-message {
		font-size: 30px;
	}
}



