.header-container {
	width: 100%;
	height: 0px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	position: fixed;
	top: 0px;
	font-size: 17px;
	font-family: Nunito;
	z-index: 500;
	text-shadow: 0 2px 8px rgba(34,34,34,.2);
}

.header-box {
	width: 85%;
}

.header-top {
	height: 50px;
	border-bottom: solid 2px #d0d4dd;
	-webkit-box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2);
	        box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.header-bottom {
	height: 50px;
}

.header-top-content {
	height: 100%;
	width: 240px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding-top: 18px;
	font-size: 20px !important;

}

.header-bottom-content {
	float: right;
	height: 100%;
	width: 300px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding-top: 5px;
}

.header-container a, span {
	color: #d0d4dd;
	text-decoration: none;
}

.header-container a:hover {
	color: #848aa5 !important;
}

.header-container span:hover {
	cursor: pointer;
	color: #848aa5 !important;
}

.header-link {
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}

.header-dropdown {
	background: none;
	border: none;
	color: #d0d4dd;
	padding: 0px;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}

.dropdown:hover {
	cursor: pointer;
}

.dropdown-item {
	color: #2b374f !important;
}

.dropdown-menu {
}

.dropdown-item {
}

.transition-out {
	height: 0px;
	-webkit-transition: 0.7s;
	-o-transition: 0.7s;
	transition: 0.7s;
	background: -webkit-gradient(linear,right top, left top,from(#4d628e),to(#32405b));
	background: -webkit-linear-gradient(right,#4d628e,#32405b);
	background: -o-linear-gradient(right,#4d628e,#32405b);
	background: linear-gradient(-90deg,#4d628e,#32405b);
}

.transition-out-show {
	height: 100vh;
}


@media (max-width: 425px) {
	.header-top-content {
		width: 100%;
	}

	.header-bottom-content {
		width: 100%;
	}
}



